import * as React from 'react';
import debounce from 'lodash.debounce';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { getManagers as get, searchManager as search } from '../../../../redux/async/employeeAsync';
// MUI Components
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';

export default function ComakerSearch(props) {

    const dispatch = useDispatch();
    const employeeRedux = useSelector((state) => state.employees.value);
    const [value, setValue] = React.useState(props.manager ?? '');

    const handleSearch = React.useCallback((event) => {
        if (event.target.value === "") {
            return false;
        }

        let params = {
            name: event.target.value
        }

        dispatch(search(params));
    }, [dispatch]);

    const handleSelect = (obj) => {
        let firstName = obj.data?.first_name ?? '';
        let lastName = obj.data?.last_name ?? '';
        let middleName = obj.data?.middle_name[0] ?? '';
        let suffix = obj.data?.suffix ?? '';

        setValue(`${firstName} ${middleName} ${lastName} ${suffix}`);
        
        if (props.setManager && obj?.label !== undefined) {
            props.setManager(obj);
        } else {
            props.setManager("");
        }
    }

    const handleDebounceSearch = React.useMemo(() => {
        return debounce(handleSearch, 400)
    }, [handleSearch]);

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            dispatch(get());
        }

        return () => {
            mounted = true;
        }
    }, [dispatch])

    return (
        <Autocomplete
            disablePortal
            autoSelect
            options={employeeRedux.managersSearchList}
            loading={employeeRedux.loading}
            loadingText="Searching..."
            noOptionsText="No Records Found"
            isOptionEqualToValue={() => true}
            onChange={(e, obj) => handleSelect(obj)}
            onKeyUpCapture={handleDebounceSearch}
            sx={{ width: '100%', mt: 0.5, mr: 1 }}
            size='small'
            fullWidth
            renderInput={(params) => <TextField label="Search" fullWidth {...params} />}
        />
    );
}

