import * as React from 'react';
// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
// Page Components
import TabPanel from './components/TabPanel2';
import PositionDataTable from '../datatable/PositionDataTable';
import BranchesDataTable from '../datatable/BranchesDataTable';
import RolesDataTable from '../datatable/RolesDataTable';
// react-icons
import { FaUserTie, FaBuilding, FaUsersCog } from 'react-icons/fa';

export default function UsersTabs() {

    const options = [
        {
            name: 'Branches',
            icon: <FaBuilding size={20} style={{ marginRight: 5 }} />,
            content: <BranchesDataTable />,
        },
        {
            name: 'Roles',
            icon: <FaUsersCog size={20} style={{ marginRight: 5 }} />,
            content: <RolesDataTable />,
        },
        {
            name: 'Positions',
            icon: <FaUserTie size={20} style={{ marginRight: 5 }} />,
            content: <PositionDataTable />,
        },
    ];
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };


    return (
        <Box
            sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', height: '100vh', width: '100vw', overflow: 'hidden' }}
        >
            <Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: '100vh', flexGrow: 1, width: 220, pt: 0, borderRight: '2px solid #eee' }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 30,
                            marginTop: 10
                        }
                    }}
                >

                    {
                        options.map((data, index) => {
                            return <Tab onClick={() => handleChange(index)} sx={{ alignItems: "start" }} key={`tabkey-${index}`} label={
                                <Typography variant="subtitle2">
                                    <Box display="flex">
                                        {data.icon}
                                        {data.name}
                                    </Box>
                                </Typography>
                            } />
                        })
                    }
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                {
                    options.map((data, index) => {
                        return <TabPanel value={value} index={index} key={`tabpanelkey-${index}`}>
                            <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                {data.content}
                            </Paper>
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    );
}
