import React from 'react';
// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get } from '../../../redux/async/positionAsync';
import { selectedPosition } from '../../../redux/slices/positionSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Typography } from '@mui/material';
// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import PositionModal from '../modals/PositionModal';
// react-icons
import { FaEdit, FaTrash, FaSlidersH, FaLightbulb } from 'react-icons/fa';


const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreate={true}
            createEvent={props.create}
            delete={props.delete}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}

export default function PositionDataTable(props) {

    const dispatch = useDispatch();
    const positionRedux = useSelector((state) => state.positions.value);
    const loading = positionRedux.loading;

    const [modalAction, setModalAction] = React.useState("");
    const [showPositionModal, setShowPositionModal] = React.useState(false);
    const toggleShowPositionModal = () => setShowPositionModal(!showPositionModal);

    const columns = [
        {
            field: 'position', headerName: 'Position', width: 350, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.position?.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Description', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', width: 200, getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleEdit = async (params) => {
        dispatch(selectedPosition(params.row));
        setModalAction("edit");
        setShowPositionModal(true);
    }

    const handleDelete = async (row) => {
        let response = await alertService.delete(1);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': [row] }))
        }
    }

    const loadPosition = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {
            loadPosition();
        }

        return () => mounted = true;

    }, [loadPosition]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            {showPositionModal && <PositionModal open={showPositionModal} toggle={toggleShowPositionModal} action={modalAction} />}
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Tips: Double-click a row to edit details
            </Typography>
            <div style={{ height: 500, paddingRight: 10 }}>
                <DataGrid
                    rows={positionRedux.positions}
                    onCellDoubleClick={(params) => handleEdit(params)}
                    columns={columns}
                    loading={loading}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        Toolbar: ToolbarOverride,
                        MoreActionsIcon: ActionsIcon
                    }}
                    componentsProps={{
                        toolbar: {
                            create: () => { setModalAction("create"); setShowPositionModal(true); dispatch(selectedPosition({})); },
                            delete: () => { handleDelete() },
                        },
                    }}
                />
            </div>
        </div>
    );
}